import '../App.css';
import Branding from '../components/Branding';

import { Button } from '@mui/material'
import React,{useEffect} from 'react'
import { useNavigate } from 'react-router-dom';
import userPool from '../userpool'
import { logout } from '../services/authenticate';
import HelpAccordian from '../components/HelpAccordian'
const MainPage = () => {

  const Navigate = useNavigate();

  useEffect(()=>{
    let user=userPool.getCurrentUser();
    console.log(user);
    if(!user){
      Navigate('/login');
    }
  },[]);

  const handleLogoout=()=>{
    logout();
  };

  const handleHome =()=>{
    Navigate('/')
  }

  return (
    <>
    <div className='App-page'>
      <div className='left-side-page'>
        <Branding/>
      </div>
  
      <div className='right-side-page'>
  <div style={{
    display: 'flex',
    justifyContent: 'flex-end',
    alignItems: 'center',
    paddingRight: '20px', // Adjust as needed for spacing
  }}>
    <div style={{ 
      position: "fixed", 
      top: "20px", 
      right: "20px", 
      zIndex: 999, 
      display: "flex", 
      gap: "10px",
    }}>
      <Button
        style={{ margin: "10px" }}
        variant='contained'
        onClick={handleHome}
      >
        Home
      </Button>

      <Button
        style={{ margin: "10px" }}
        variant='contained'
        onClick={handleLogoout}
      >
        LOGOUT
      </Button>
    </div>
  </div>

  <div style={{
    position: 'relative',
    marginTop: '50px', // Adjust to ensure content is not cut off
  }}>
    <HelpAccordian/>
  </div>
</div>

    </div>
  </>
  
    
  )
}

export default MainPage
























