import React, { useState } from 'react';
import {
  Avatar,
  Button,
  CssBaseline,
  TextField,
  Box,
  Typography,
  Container,
  Link, 
  Grid
} from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import { useNavigate } from 'react-router-dom';
import { authenticate, forgotPassword } from '../services/authenticate'; // Import the forgotPassword function
import graphic from '../img/graphic.svg'
import logo from '../img/logo.png'
import loginImg from '../img/tbm_logo2.png'

const defaultTheme = createTheme();

export default function SignIn() {
  const navigate = useNavigate();

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [emailErr, setEmailErr] = useState('');
  const [passwordErr, setPasswordErr] = useState('');
  const [loginErr, setLoginErr] = useState('');
  const [isPasswordReset, setIsPasswordReset] = useState(false);
  const [cognitoUser, setCognitoUser] = useState(null);

const [verificationCode, setVerificationCode] = useState('');
const [verificationCodeErr, setVerificationCodeErr] = useState('');
const [newPasswordErr, setNewPasswordErr] = useState('');
const handleVerificationCodeChange = (event) => {
  setVerificationCode(event.target.value);
};

const handleNewPasswordChange = (event) => {
  setNewPassword(event.target.value);
};

const handleSetNewPassword = (event) => {
  event.preventDefault();
  setVerificationCodeErr('');
  setNewPasswordErr('');
  
  if (!verificationCode) {
    setVerificationCodeErr('Verification code is required');
    return;
  }

  if (!newPassword) {
    setNewPasswordErr('New password is required');
    return;
  }

  if (newPassword.length < 8) {
    setNewPasswordErr('Password must be at least 8 characters long');
    return;
  }

  if (cognitoUser) {
    cognitoUser.confirmPassword(verificationCode, newPassword, {
      onSuccess: () => {
        setLoginErr('');
        navigate('/');
      },
      onFailure: (err) => {
        setLoginErr(err.message);
      },
    });
  }
};
  const formInputChange = (formField, value) => {
    if (formField === 'email') {
      setEmail(value);
    }
    if (formField === 'password') {
      setPassword(value);
    }
    if (formField === 'newPassword') {
      setNewPassword(value);
    }
  };

  const validation = () => {
    return new Promise((resolve) => {
      if (email === '' && password === '') {
        setEmailErr('Email is Required');
        setPasswordErr('Password is required');
        resolve({ email: 'Email is Required', password: 'Password is required' });
      } else if (email === '') {
        setEmailErr('Email is Required');
        resolve({ email: 'Email is Required', password: '' });
      } else if (password === '') {
        setPasswordErr('Password is required');
        resolve({ email: '', password: 'Password is required' });
      } else if (password.length < 8) {
        setPasswordErr('must be 8 characters');
        resolve({ email: '', password: 'must be 8 characters' });
      } else {
        resolve({ email: '', password: '' });
      }
    });
  };

  const handleClick = (event) => {
    event.preventDefault();
    setEmailErr('');
    setPasswordErr('');
    validation()
      .then((res) => {
        if (res.email === '' && res.password === '') {
          authenticate(email, password)
            .then(() => {
              setLoginErr('');
              navigate('/');
            })
            .catch((err) => {
              if (err.name === 'PasswordResetRequiredException') {
                setIsPasswordReset(true);
                setCognitoUser(err.user);
              } else {
                setLoginErr(err.message);
              }
            });
        }
      })
      .catch((err) => console.log(err));
  };

  const handlePasswordReset = (event) => {
    event.preventDefault();
    if (cognitoUser && newPassword) {
      cognitoUser.completeNewPasswordChallenge(newPassword, {}, {
        onSuccess: () => {
          setLoginErr('');
          navigate('/');
        },
        onFailure: (err) => {
          setLoginErr(err.message);
        },
      });
    } else {
      setLoginErr('New password is required');
    }
  };

  const handleForgotPassword = (event) => {
    event.preventDefault();
    forgotPassword(email)
      .then((response) => {
        // Handle success, e.g., show a message to the user
        console.log(response);
      })
      .catch((error) => {
        // Handle error, e.g., display an error message
        console.error(error);
      });
  };

  return (
    <div className='pb-5'>
    <ThemeProvider theme={defaultTheme}>
      <div className="h-22 bg-rei-blue">
      <img src={logo} alt="Custom Icon" class="w-24 h-auto pl-4" />
      </div>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <img src={loginImg} alt="Custom Icon" />
          <br/>
          <Typography component="h1" variant="h5">
            {isPasswordReset ? 'Reset Password' : ''}
          </Typography>
          <Box component="form" onSubmit={isPasswordReset ? handlePasswordReset : handleClick} noValidate sx={{ mt: 1 }}>
            {!isPasswordReset ? (
              <>
                <TextField
                  margin="normal"
                  required
                  fullWidth
                  id="email"
                  label="Email Address"
                  name="email"
                  autoComplete="email"
                  autoFocus
                  value={email}
                  onChange={(e) => formInputChange('email', e.target.value)}
                  helperText={emailErr}
                  error={!!emailErr}
                />
                <TextField
                  margin="normal"
                  required
                  fullWidth
                  name="password"
                  label="Password"
                  type="password"
                  id="password"
                  autoComplete="current-password"
                  value={password}
                  onChange={(e) => formInputChange('password', e.target.value)}
                  helperText={passwordErr}
                  error={!!passwordErr}
                />
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{ mt: 3, mb: 2 }}
                >
                  Sign In
                </Button>
              </>
            ) : (
              <>
                <TextField
                  margin="normal"
                  required
                  fullWidth
                  name="newPassword"
                  label="New Password"
                  type="password"
                  id="newPassword"
                  autoComplete="new-password"
                  value={newPassword}
                  onChange={(e) => formInputChange('newPassword', e.target.value)}
                />
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{ mt: 3, mb: 2 }}
                >
                  Set New Password
                </Button>
              </>
            )}
            <Typography variant="body2" color="error" align="center">
              {loginErr}
            </Typography>
            <Grid container>
              <Grid item xs>
                {/* Add onClick event handler to trigger the forgotPassword function */}
                <Link href="#" variant="body2" onClick={handleForgotPassword}>
                  Forgot password?
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
    </div>
  
  );
}
