import { useState } from 'react';
import axios from 'axios';

const useFileUploader = () => {
    const [progress, setProgress] = useState(0);
    const [fileUploaded, setFileUploaded] = useState(false);
    const [error, setError] = useState(null);
    const bucket = "tbm-test-bucket"; 

    const uploadFile = async (fileName, file) => {
        const formData = new FormData();
        formData.append("file", file);
        
        try {
            // Make a PUT request to the API endpoint
            await axios.put(`https://fzsgsd8wlc.execute-api.us-east-1.amazonaws.com/v1/${bucket}/${fileName}`, formData, {
                onUploadProgress: (progressEvent) => {
                    const uploadPercentage = Math.round((progressEvent.loaded / progressEvent.total) * 100);
                    setProgress(uploadPercentage);
                }
            });
            
            setFileUploaded(true);
            console.log("File uploaded successfully");
        } catch (error) {
            setError(error.message);
        }
    };

    return { progress, fileUploaded, error, uploadFile };
};

export default useFileUploader;
