import '../App.css';
import { ImUpload3 } from "react-icons/im";
import { useState, useEffect } from 'react';
import ProgressBar from '../components/ProgressBar';
import useFileUploader from '../hooks/useFileUploader';
import useExcelCsvUploader from '../hooks/useExcelCsvUploader';
import useModel from '../hooks/useModel';
import Spinner from '../components/Spinner';


export default function FileUploadForm() {
    const [fileName, setFileName] = useState("No file");
    const [modelCompleted, setModelCompleted] = useState(false);
    const [urlOpened, setUrlOpened] = useState(false); 
    const { progress, fileUploaded, error, uploadFile } = useFileUploader();
    const { response, conversionError, uploadExcelCsv } = useExcelCsvUploader();
    const { url, modelError, progressModel, loading, runModel } = useModel();
    const [modelErrorHandled, setModelErrorHandled] = useState(false); //handles model error
    const [conversionErrorHandled, setConversionErrorHandled] = useState(false);//handles excel csv conversion error 
    const[errorHandled, setErrorHandled] = useState(false); 


    useEffect(() => {
        if (modelCompleted && url && !urlOpened) {
            window.open(url); // Open URL in new tab
            setUrlOpened(true); // Mark URL as opened
            window.location.href = '/';
        }
    }, [modelCompleted, url, urlOpened]);

    useEffect(()=>{
        if (modelError && !modelErrorHandled) {
            alert(`Error: ${modelError.message}. Please try again.`); //error handling
            setModelErrorHandled(true);
            window.location.href = '/';
        }
    },[modelError, modelErrorHandled]);

    useEffect(()=>{
        if (error && !errorHandled) {
            alert(`Error: ${error}. Something might've went wrong in uploading the file.`); //error handling
            setErrorHandled(true);
            window.location.href = '/';
        }
    },[error, errorHandled]);

    useEffect(()=>{
        if (conversionError && !conversionErrorHandled) {
            alert(`Error: ${conversionError}. Something might've went wrong in transforming the file.`); //error handling
            setConversionErrorHandled(true);
            window.location.href = '/';
        }
    },[conversionError, conversionErrorHandled]);

    const handleFileChange = (event) => {
        const file = event.target.files[0];
        if (file) {
            setFileName(file.name);
        } else {
            setFileName("No file");
        }
    };
    

    const handleSubmit = async () => {
        if ((fileName === "No file"))  {
            alert("Please fill out all info before proceeding.");
        } else {
            const file = document.getElementById("file-upload").files[0];
            const fileNameWithoutExtension = fileName.split('.').slice(0, -1).join('.');
            await uploadFile(fileName, file); // Wait for uploadFile to complete
            await uploadExcelCsv(fileName); // Wait for uploadExcelCsv to complete
            await runModel(fileNameWithoutExtension); // Wait for model run to complete
            setModelCompleted(true); // Mark model as completed 
        }
    };

    let content = (
        <div className="right-side-page">
      <div style={{ paddingLeft: '20px' }}>
        <div className='text-2xl'>Welcome to the TBM Automated Classifier</div>
        <br />
        <h1>The TBM Automated Classifier is a platform that leverages machine learning for categorization, specializing in data tagging for the TBM Taxonomy. The platform automatically categorizes and organizes your data according to the TBM Cost Pool and IT Towers standards, ensuring accurate and efficient data management.</h1>
        <br />
        <h1>This process enhances the precision and usability of your data, facilitating better analysis and decision-making, as well as greater consistency and compliance with the TBM Taxonomy framework.</h1>
        <br />
        <hr />
        <br />
        <label className='font-light font-semibold'>
          Upload Subtower Input<br />
          <small className='mb-10 font-thin'>(File formats accepted: .csv, .xlsx/xls - Max file size: 20MB)</small>
          <br />
        </label>
        <div className="fileupload mt-5 border border-gray-200" style={{ paddingLeft: '0px' }}>
          <label htmlFor="file-upload" className="file-select-button">
            <span className='icon-container'>
              <ImUpload3 size={20} color='white' />
            </span>
            Choose File
          </label>
          <input id="file-upload" type="file" name="fileupload" accept=".csv,application/vnd.ms-excel,.xls,.xlsx"
            className="hidden file-upload-input" onChange={handleFileChange} />
          <div className='file-name-container'>
            {fileName && <p className="selected-file">Selected: {fileName}</p>}
          </div>
        </div>
        <button className='nav-button' onClick={handleSubmit}>Submit</button>
      </div>
    </div>

    );

    if (fileUploaded === true) {
        content = (
            <div>
                <label className='font-light font-semibold text-xl'>
                    Thank you for submitting the file!<br />
                    <p className='font-thin text-lg'>Your file will download when complete.</p>
                </label>
                <br />
                {loading && <Spinner />}
            </div>
        );
    }

    return (
        <div>
            {content}
        </div>
    );
}
