import { useState } from 'react';
import axios from 'axios';

const useExcelCsvUploader = () => {
    const [response, setResponse] = useState(null);
    const [conversionError, setConversionError] = useState(null);

    const uploadExcelCsv = async (fileName) => {
        const payload = {
            file: `s3://tbm-test-bucket/${fileName}`,
            location: "data_csv/"
        };

        try {
            const response = await axios.post('https://i2qhse970a.execute-api.us-east-1.amazonaws.com/test/excel-csv', payload);
            setResponse(response.data);
        } catch (error) {
            setConversionError(error.message);
        }
    };

    return { response, conversionError, uploadExcelCsv };
};

export default useExcelCsvUploader;
