import { useState } from 'react';
import axios from 'axios';

const useModel = () => {
    const [url, setUrl] = useState(null);
    const [modelError, setModelError] = useState(null);
    const [progressModel, setProgressModel] = useState(0);
    const [loading, setLoading] = useState(false); // Add loading state

    const runModel = async (file) => {
        setLoading(true); // Set loading to true when API call starts

        const payload = {
            file: `s3://tbm-test-bucket/data_csv/${file}.csv`,
            columns: "Description_Award/Description_Line"
        };

        try {
            const response = await axios.put('https://w4glu2px4dcglfhp57ki5zo6aa0cucfh.lambda-url.us-east-1.on.aws/', payload, {
                timeout: 300000,
                onUploadProgress: progressEvent => {
                    const { loaded, total } = progressEvent;
                    const percentCompleted = Math.round((loaded * 100) / total);
                    setProgressModel(percentCompleted);
                },
            });
            const responseData = response.data;
            const predictionFileUrl = responseData['prediction file'];
            setUrl(predictionFileUrl);
        } catch (error) {
            if (error.response && error.response.status === 403) {
                const customErrorMessage = error.response.data;
                setModelError(customErrorMessage || 'API Forbidden: You do not have permission to access this resource');
            } else {
                setModelError(error.message);
            }
        } finally {
            setLoading(false); // Set loading to false when API call finishes
            setProgressModel(0);
        }
    };

    return { url, modelError, progressModel, loading, runModel };
};

export default useModel;
