import * as React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';

export default function HelpAccordian() {
  return (
    <div className="right-side-page">
      <div className='text-3xl'> Frequently Asked Questions</div>
      <hr/>
      <br/>
      <br/>
      <div className='text-xl'> Basics </div>
      <br/>
      <Accordion className="accordion">
        <AccordionSummary
          expandIcon={<ArrowDropDownIcon />}
          aria-controls="panel1-content"
          id="panel1-header"
        >
          <Typography><b>What is TBM Automated Collector?</b></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            The TBM Automated Classifier is a platform that leverages machine
            learning for categorization, specializing in data tagging for the TBM
            Taxonomy. The platform automatically categorizes and organizes your data
            according to the TBM Cost Pool and IT Towers standards, ensuring accurate
            and efficient data management.
          </Typography>
          <Typography>
            This process enhances the precision and usability of your data,
            facilitating better analysis and decision-making, as well as greater
            consistency and compliance with the TBM Taxonomy framework.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <br/>
      <br/>
      <div className='text-xl'> Login </div>
      <br/>
      <Accordion className="accordion">
        <AccordionSummary
          expandIcon={<ArrowDropDownIcon />}
          aria-controls="panel2-content"
          id="panel2-header"
        >
          <Typography><b>What is the URL of TBM Automated Classifier?</b></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            <a href="https://tbm.reisystems.com">https://tbm.reisystems.com</a>
          </Typography>
        </AccordionDetails>
      </Accordion>

      <Accordion className="accordion">
        <AccordionSummary
          expandIcon={<ArrowDropDownIcon />}
          aria-controls="panel3-content"
          id="panel3-header"
        >
          <Typography><b>How do I obtain an account to access <i>TBM Automated Classifier</i>?</b></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Contact Indunil Ranaviraja (<a href="mailto:indunil.ranaviraja@reisystems.com">indunil.ranaviraja@reisystems.com</a>) or Rohini Iyer (<a href="mailto:rohini.iyer@reisystems.com">rohini.iyer@reisystems.com</a>).
          </Typography>
        </AccordionDetails>
      </Accordion>

      <Accordion className="accordion">
        <AccordionSummary
          expandIcon={<ArrowDropDownIcon />}
          aria-controls="panel4-content"
          id="panel4-header"
        >
          <Typography><b>What is the user timeout session?</b></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            The user session will timeout in 30 minutes.
          </Typography>
        </AccordionDetails>
      </Accordion>

      <Accordion className="accordion">
        <AccordionSummary
          expandIcon={<ArrowDropDownIcon />}
          aria-controls="panel5-content"
          id="panel5-header"
        >
          <Typography><b>What is the password policy?</b></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            <ul>
              <li>8-character minimum length</li>
              <li>Contains at least 1 number</li>
              <li>Contains at least 1 lowercase letter</li>
              <li>Contains at least 1 uppercase letter</li>
              <li>Contains at least 1 special character from the following set, or a non-leading, non-trailing space character</li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>
      <br/>
      <br/>
      <div className='text-xl'> Functionality </div>
      <br/>
      <Accordion className="accordion">
        <AccordionSummary
          expandIcon={<ArrowDropDownIcon />}
          aria-controls="panel6-content"
          id="panel6-header"
        >
          <Typography><b>What is the file size limit for the subtower input file?</b></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            The maximum file size limit is currently 20 MB, but will be increased to 200 MB in our next release.
          </Typography>
        </AccordionDetails>
      </Accordion>

      <Accordion className="accordion">
        <AccordionSummary
          expandIcon={<ArrowDropDownIcon />}
          aria-controls="panel7-content"
          id="panel7-header"
        >
          <Typography><b>What file types are supported for the subtower input file?</b></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Currently, CSV or Microsoft Excel (.xlsx/.xls) formats are supported.
          </Typography>
        </AccordionDetails>
      </Accordion>

      <Accordion className="accordion">
        <AccordionSummary
          expandIcon={<ArrowDropDownIcon />}
          aria-controls="panel8-content"
          id="panel8-header"
        >
          <Typography><b>What is the file format required for the subtower input file?</b></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            TBD
          </Typography>
        </AccordionDetails>
      </Accordion>
      <br/>
      <br/>
      <div className='text-xl'> Troubleshooting </div>
      <br/>
      <Accordion className="accordion">
        <AccordionSummary
          expandIcon={<ArrowDropDownIcon />}
          aria-controls="panel9-content"
          id="panel9-header"
        >
          <Typography><b>What are the supported browsers?</b></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            The latest builds of Microsoft Edge (v125 or higher), Google Chrome (v125 or higher), and Mozilla Firefox (v126 or higher) are supported.
          </Typography>
        </AccordionDetails>
      </Accordion>

      <Accordion className="accordion">
        <AccordionSummary
          expandIcon={<ArrowDropDownIcon />}
          aria-controls="panel10-content"
          id="panel10-header"
        >
          <Typography><b>Why cant I download the file?</b></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            <ul>
              <li>Pop-ups must be enabled in your browser.</li>
              <li>
                Click on any of the links for instructions on how to allow pop-ups for the URL in your browser:
                <ul>
                  <li><a href="https://support.microsoft.com/en-us/microsoft-edge/block-pop-ups-in-microsoft-edge-1d8ba4f8-f385-9a0b-e944-aa47339b6bb5">Microsoft Edge</a></li>
                  <li><a href="https://support.google.com/chrome/answer/95472#zippy=%2Callow-pop-ups-and-redirects-from-a-site">Google Chrome</a></li>
                  <li><a href="https://support.mozilla.org/en-US/kb/pop-blocker-settings-exceptions-troubleshooting#w_pop-up-blocker-settings">Mozilla Firefox</a></li>
                </ul>
              </li>
            </ul>
          </Typography>
        </AccordionDetails>
      </Accordion>

      <Accordion className="accordion">
        <AccordionSummary
          expandIcon={<ArrowDropDownIcon />}
          aria-controls="panel11-content"
          id="panel11-header"
        >
          <Typography><b>Who can I reach out to for support?</b></Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Contact Indunil Ranaviraja (<a href="mailto:indunil.ranaviraja@reisystems.com">indunil.ranaviraja@reisystems.com</a>) or Rohini Iyer (<a href="mailto:rohini.iyer@reisystems.com">rohini.iyer@reisystems.com</a>).
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
