import logo from '../img/logo.png';
import '../App.css';
import graphic from '../img/graphic.svg'
export default function Branding() {
  return (
    <>
      <img src={logo} className="App-logo" alt="logo" />

      <div className="text-container">
        <img src={graphic} alt="logo" className="branding-image" /> {/* Add a class */}
        <h1 className="text-left-main">TBM Automated Classifier</h1>
        <p className="text-base pt-5 text-white">
        Accelerate your TBM adoption with Machine Learning automated mapping.
        </p>
      </div>
      <p className="copy-right"> 2024 REI Systems, Inc.</p>
    </>
  );
}

